.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;

	@media screen and (min-width:320px) {
		width: 320px;
	}
  
	@media screen and (min-width: 480px) {
	  width: 480px;
	}
  
	@media screen and (min-width: 768px) {
	  width: 768px;
	}
  
	@media screen and (min-width: 1280px) {
	  width: 1280px;
	}
  }